@import '../../styles/mixins';

// SB Date Input Variables:
// --sb-date-input-item-size - size for each item in the popper
// --sb-date-input-day-margin - margin between items
// --sb-date-input-font-size - font size
// --sb-date-input-hover-border - border color on hover

// Overriding react-datepicker variables, cannot control the name patterns they use
/* stylelint-disable scss/dollar-variable-pattern */
$datepicker__font-family: var(--sbt-font-stack-primary);
$datepicker__item-size: var(--sb-date-input-day-margin, 2.25rem);
$datepicker__day-margin: var(--sb-date-input-day-margin, 0.177rem);
$datepicker__font-size: 0.8rem;
/* stylelint-enable scss/dollar-variable-pattern */

@import 'react-datepicker/src/stylesheets/datepicker';

/* stylelint-disable selector-class-pattern -- Third Party Classes */
.react-datepicker-wrapper {
  width: unset;
}

.react-datepicker {
  box-shadow: 1px 1px 4px 2px var(--sbt-theme-tertiary-color-30);

  .tooltip-arrow[data-placement*='bottom']::after {
    border-color: transparent transparent #d40909 !important;
  }

  .react-datepicker__month {
    .react-datepicker__month-text {
      padding: var(--sbt-spacing-20);
    }
  }
}

.react-datepicker-popper {
  .react-datepicker .react-datepicker__triangle::after {
    border-bottom-color: var(--sbt-white);
  }
}

.react-datepicker__header {
  background-color: var(--sbt-white);
  border-bottom: none;
  color: $datepicker__muted-color;
  padding-top: $datepicker__font-size;

  .react-datepicker__current-month {
    color: $datepicker__muted-color;
  }

  .react-datepicker__day-names {
    padding-top: $datepicker__margin;

    .react-datepicker__day-name {
      color: $datepicker__muted-color;
    }
  }
}

.react-datepicker__day {
  border: 1px solid transparent;
  border-radius: 50%;

  &:hover,
  &:focus {
    background-color: var(--sbt-white);
    border-color: var(--sbt-theme-color-50);
    border-radius: 50%;
    color: $datepicker__text-color;
    outline: 0;
  }

  &--selected {
    background-color: var(--sbt-theme-color-50);
    border-color: var(--sbt-theme-color-50);
    color: #fff;
  }

  &--keyboard-selected {
    background-color: inherit;
    color: inherit;

    &:hover {
      background-color: var(--sbt-white);
    }
  }

  &:active {
    background-color: var(--sbt-theme-color-80);
    border-color: var(--sbt-theme-color-80);
    color: var(--sbt-white);
  }

  &--disabled:hover {
    background-color: transparent;
    color: $datepicker__muted-color;
  }

  &--highlighted {
    border-color: $datepicker__highlighted-color;
  }
}

.react-datepicker__today-button {
  background-color: var(--sbt-white);
  border-radius: inherit;
  border-top: none;
  clear: left;
  color: $datepicker__muted-color;
  cursor: pointer;
  font-weight: var(--sbt-font-weight-70);
  padding: calc(#{$datepicker__item-size}/ 2);
  text-align: left;
}

.sb-date-input {
  --sb-button-padding-vertical-small: var(--sbt-spacing-20);
  background-color: var(--sbt-white);
  border: var(--sbt-input-border-size) solid var(--sbt-input-border-color);
  border-radius: var(--sbt-border-radius-small);
  display: flex;
  justify-content: space-between;
  min-height: unset;
  transition: all 80ms cubic-bezier(0.2, 0, 0.4, 0.9);
  width: 236px;

  &:hover {
    border-color: var(
      --sb-date-input-hover-border,
      var(--sbt-theme-tertiary-color-30)
    );
  }

  .sb-input {
    border: none;
    height: calc(var(--sbt-input-height) - 2px);

    &:focus {
      box-shadow: none;
    }
  }

  .sb-button {
    &:focus {
      box-shadow: none;
    }

    &:hover {
      background-color: none;
    }

    .fa-calendar {
      opacity: 0.8;

      &:hover {
        opacity: 1;
      }
    }
  }

  .sb-button_demoted:hover {
    background-color: transparent;
  }
}

.sb-date-input_disabled {
  @include disabled-shake();
  background-color: var(--sbt-color-disabled-background);
  border-color: var(--sbt-color-disabled-border);
  cursor: not-allowed;

  &:hover {
    background-color: var(--sbt-theme-tertiary-color-10);

    .sb-input {
      background-color: var(--sbt-theme-tertiary-color-10);
    }
  }

  .sb-input:disabled:hover {
    border: none;
  }

  .fa-calendar {
    cursor: not-allowed;
    opacity: 0.4;

    &:hover {
      opacity: 0.4;
    }
  }
}

.sb-date-input__value-container {
  padding: 0 0.5rem;
}

.sb-date-input__placeholder {
  font-size: var(--sbt-font-size-30);
  line-height: var(--sbt-line-height-100);
  margin: 0;
  max-width: calc(100% - 1rem);
}

.sb-date-input__placeholder {
  color: var(--sbt-theme-tertiary-color-90);
}

.sb-date-input__indicator-separator {
  background-color: var(--sbt-theme-tertiary-color-30);
}

.sb-date-input__indicator {
  color: var(--sbt-theme-tertiary-color-30);
  padding: 0.375rem;

  &:hover {
    color: var(--sbt-theme-tertiary-color-50);
  }
}

.sb-date-input__control--is-focused {
  border-color: transparent;
  box-shadow: var(--sbt-input-box-shadow);
  outline: none;
  outline-color: var(--sbt-theme-color-40);

  &:hover {
    border-color: transparent;
  }

  .sb-date-input__indicator {
    color: var(--sbt-theme-tertiary-color-50);

    &:hover {
      color: var(--sbt-theme-tertiary-color-60);
    }
  }
}

.react-datepicker__tab-loop {
  display: inline-block;
}

/* Extra specifificy to compete with react-datepicker */
.sb-date-input.sb-date-input_error {
  @include error();
  padding: var(--sbt-spacing-0);

  &.sb-date-input__control--is-focused {
    border-color: var(--sbt-color-error-50) !important;
    box-shadow: var(--sbt-elevation-error);
  }
}
